            transform: scaleY(0.98) translateY(10px);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}

@keyframes enter {
  from {
    opacity: 0;
    -webkit-transform: scaleY(0.98) translateY(10px);
            transform: scaleY(0.98) translateY(10px);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}
